.trip {
    margin: 4rem 6rem;
    color: #2a2a2a;
    text-align: center;
}

.trip h1{
    font-size: 3rem;
}

.tripcard{
    margin-top: 3rem;
    display: flex;
    justify-content: space-between;
}

.tcard{
    width: 32%;
    text-align: start;
    box-shadow: 0 5px 25px 2px rgba(0 ,0 ,0 , 0.11);
    border-radius: 7px;
    cursor: pointer;
    padding: 1rem 0.8rem;
}

.tripimg{
    height: 200px;
    overflow: hidden;
    border-radius: 7px;
}

.tripimg img {
    width: 100%;
    height: 100%;
    transition: 0.3s ease-in-out;
    border-radius: 7px;
}

.tripimg:hover img{
    transform: scale(1.3);
}

.tcard h4{
    font-size: 1.3rem;
    padding: 0.9rem 0 0.2rem 0;
}

@media screen and (max-width:850px) {
    .trip{
        margin: 4rem 2rem;
    }

    .tripcard{
        flex-direction: column;
    }
    .tcard{
        width: 100%;
        margin-bottom: 1.5rem;
    }
}






















