.hero{
    width: 100%;
    height: 100vh;
    position: relative;
}
.hero-mid{
    width: 100%;
    height: 70vh;
    position: relative;
}

.hero-mid h1{
    font-size: 4rem;
}

img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.herotxt{
    position: absolute;
    top: 55%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    text-align: center;
}

.herotxt h1{
    font-size: 3rem;
    font-weight: 800;
    color: #fff;
    --webkit-background-clip: text;
    --webkit-text-fill-color: transparent;
}

.herotxt p{
    font-size: 1.3rem;
    color: #fff;
    padding: 0.5rem 0 2rem 0;
}

.herotxt .show{
    text-decoration: none;
    background-color: #fff;
    padding: 1rem 2rem;
    border-radius: 6px;
    font-size: 1.1rem;
    font-weight: bold;
    letter-spacing: 2px;
    color: #222;
}

.herotxt .hide{
    display: none;
}

@media screen and (max-width: 555px) {
    .herotxt h1{
        padding: 10px 20px;
    }

    .herotxt p{
        font-size: 1.1rem;
        padding: 0 0 2rem 0;
    }

    .herotxt .show {
        padding: 0.6rem 1.1rem;
        border-radius: 6px;
        font-size: 1rem;
    }
}








